.header {
    padding: 16px 0;
    overflow: hidden;
    position: fixed;
    box-shadow: 0 2px 8px 0 rgba(99, 99, 99, 0.12);
    width: 100%;
    height: 40px;
    max-height: 40px;
    top: 0;
    background-color: white;
    z-index: 100;
    display: flex;
    align-items: center;
    &__logo {
        width: 200px;
        max-width: 200px;
        height: auto;
        image-rendering: -webkit-optimize-contrast;
        margin-left: 26px;
        margin-right: 26px;
        @media screen and (max-width: 450px) {
            width: 160px;
        }
    }
}